import Event from "@/common/tools/watcher.js"
import tableRow from "@/components/table-row.vue"
export default {
	components: {
		tableRow
	},
	props: {
		columns: {
			type: Array,
			required: true
		},
		list: {
			type: Array,
			required: true
		},
		rowClassName:{
			type:[String,Function],
			default:""
		},
		'slot-cols':{
			type:Array,
			default:()=>{
				return []
			}
		},
		"span-method":{
			type:Function,
			default:()=>{
				return ()=>{
					return {
						rowspan:1,
						colspan:1
					}
				}
			}
		},
		"td-width": {
			type: Number,
			default: 110
		},
		"td-height": {
			type: Number,
			default: 30
		},
		"td-padding": {
			type: Number,
			default: 10
		},
		"border-color": {
			type: String,
			default: "#666"
		}
	},

	created() {
		console.log(this.tdHeight)

	},
	methods:{
		rowClassNamePlus(row,index){
			if(typeof this.rowClassName==="string"){
					return this.rowClassName;
			}
			else if(typeof this.rowClassName==="function"){
				return this.rowClassName(row,index);
			}
		},
		pullEvent(event,data){
				console.log("触发时间")
				console.log(event)
				console.log(data)
				this.$emit(event,data);
		}
	}
}
